<template>
  <div class="container">
    <div class="row">
      <passage-card class="clo-sm-12 col-md-4 col-lg-3 mb-3" v-for="item in drafts" :passage="item"
                    :key="item.objectId"></passage-card>
    </div>
  </div>
</template>

<script>
import PassageCard from "./PassageCard";
export default {
  name: "DraftManager",
  components: {PassageCard},
  data(){
    return{
      drafts:[]
    }
  },
  mounted() {
    this.getDraftList()
  },
  methods:{
    getDraftList() {
      const query = Bmob.Query("DraftList");
      query.order("-createdAt");
      query.find().then(res => {
        this.drafts = res
        console.table(res)
      });
    }
  }
}
</script>

<style scoped>

</style>



